body {
  max-width: 100%;
  background: #fbfafa;
}

button {
  color: #ccc;
  width: 100px;
  height: 30px;
  background: #333;
  border: 0;
  border-radius: 4px;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  transition: all .6s;
  overflow: hidden;
}

button:focus {
  outline: 0;
}

button:before {
  content: "";
  width: 60px;
  height: 100%;
  opacity: .5;
  filter: blur(30px);
  background: rgba(255, 255, 255, .5);
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateX(-100px)skewX(-15deg);
}

button:after {
  content: "";
  width: 30px;
  height: 100%;
  opacity: 0;
  filter: blur(5px);
  background: rgba(255, 255, 255, .2);
  display: block;
  position: absolute;
  top: 0;
  left: 30px;
  transform: translateX(-100px)skewX(-15deg);
}

button:hover {
  cursor: pointer;
  background: #338033;
}

button:hover:before {
  opacity: .6;
  transition: all .7s;
  transform: translateX(300px)skewX(-15deg);
}

button:hover:after {
  opacity: 1;
  transition: all .7s;
  transform: translateX(300px)skewX(-15deg);
}

